<template>
  <header>
    <div id="cLogo">
      <img id="logo" src="../../../public/img/logo.webp" alt="img de LOGO" />
      <h1>Proyectos de arquitectura e ingeniería</h1>

      <div id="menuIcon" :class=" classMenu" @click="showMenu = !showMenu">
        <div class="bar1"></div>
        <div class="bar2"></div>
        <div class="bar3"></div>
      </div>
    </div>

    <nav id="menu" v-show="showMenu">
      <ul>
        <li>
          <a
            :class="'button ' + classActiveButton[0]"
            href="#home"
            @click="clickMenu = !clickMenu"
          >Inicio</a>
        </li>

        <li>
          <a
            :class="'button ' + classActiveButton[1]"
            href="#about"
            @click="clickMenu = !clickMenu"
          >Nosotros</a>
        </li>

        <li>
          <a
            :class="'button ' + classActiveButton[2]"
            href="#proyects"
            @click="clickMenu = !clickMenu"
          >Proyectos</a>
        </li>

        <li>
          <a
            :class="'button ' + classActiveButton[3]"
            href="#services"
            @click="clickMenu = !clickMenu"
          >Servicios</a>
        </li>

        <li>
          <a
            :class="'button ' + classActiveButton[4]"
            href="#contact"
            @click="clickMenu = !clickMenu"
          >Contacto</a>
        </li>

        <li class="rss">
          <a href @click="clickMenu = !clickMenu">
            <img
              id="imgFacebook"
              src="@/assets/icons/facebook.png"
              alt="Img Facebook"
              title="Facebook"
            />
          </a>

          <a href @click="clickMenu = !clickMenu">
            <img
              id="imgInstagram"
              src="@/assets/icons/instagram.png"
              alt="Img Instagram"
              title="Instagram"
            />
          </a>
        </li>
      </ul>
    </nav>
  </header>
</template>

<script>
export default {
  name: "Header",
  props: { windowWidth: Number },
  data() {
    return {
      showMenu: false,
      clickMenu: false,
      classMenu: "",
      classActiveButton: ["", "", "", "", ""]
    };
  },
  mounted() {
    this.showMenuFun();
    this.activeButton();
  },
  computed: {
    route() {
      return this.$route.hash;
    }
  },
  methods: {
    showMenuFun() {
      if (this.windowWidth > 1080) {
        this.showMenu = true;
      } else {
        if (this.clickMenu == true) {
          this.clickMenu = false;
        }
        this.showMenu = false;
      }
    },
    activeButton() {
      switch (this.route) {
        case "#home":
          this.classActiveButton = ["active", "", "", "", ""];
          break;

        case "#about":
          this.classActiveButton = ["", "active", "", "", ""];
          break;

        case "#proyects":
          this.classActiveButton = ["", "", "active", "", ""];
          break;

        case "#services":
          this.classActiveButton = ["", "", "", "active", ""];
          break;

        case "#contact":
          this.classActiveButton = ["", "", "", "", "active"];
          break;
      }
    }
  },
  watch: {
    windowWidth() {
      this.showMenuFun();
    },
    clickMenu: "showMenuFun",
    showMenu() {
      if (this.showMenu === true) {
        this.classMenu = "change";
      } else {
        this.classMenu = "";
      }
    },
    route: "activeButton"
  }
};
</script>

