<template>
  <section id="proyects">
    <div id="separationProyects"></div>

    <div id="containerProyects">
      <h2>Habitacional</h2>
      <section id="housingProyects" class="proyects">
        <router-link :to="{ name: 'DetailsProyect', params: {name: db.housings[0].url} }">
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseHousing+ 'Housing-1/'+db.housings[0].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseHousing+ 'Housing-1/'+db.housings[0].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseHousing+ 'Housing-1/'+db.housings[0].homeImg[0]"
                :alt="db.housings[0].name"
              />
            </picture>

            <figcaption>{{ db.housings[0].name }}</figcaption>
          </figure>
        </router-link>

        <router-link
          :to="{ name: 'DetailsProyect', params: { name: db.housings[1].url } }"
          style="opacity:0.2; cursor: no-drop;"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseHousing+ 'Housing-2/'+db.housings[1].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseHousing+ 'Housing-2/'+db.housings[1].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseHousing+ 'Housing-2/'+db.housings[1].homeImg[0]"
                :alt="db.housings[1].name"
              />
            </picture>

            <figcaption>{{ db.housings[1].name }}</figcaption>
          </figure>
        </router-link>

        <router-link :to="{ name: 'DetailsProyect', params: {name: db.housings[2].url} }">
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseHousing+ 'Housing-3/'+db.housings[2].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseHousing+ 'Housing-3/'+db.housings[2].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseHousing+ 'Housing-3/'+db.housings[2].homeImg[0]"
                :alt="db.housings[2].name"
              />
            </picture>

            <figcaption>{{ db.housings[2].name }}</figcaption>
          </figure>
        </router-link>
      </section>

      <h2>Comercial</h2>
      <section id="commercialProyects" class="proyects">
        <router-link
          id="commercialP1"
          :to="{ name: 'DetailsProyect', params: {name: db.commercials[0].url} }"
          style="opacity:0.2; cursor: no-drop;"
        >
          <figure>
            <picture>
              <!-- <source
                media="(max-width:900px)"
                :srcset="urlBaseCommercial+ 'Commercial-1/'+db.commercials[0].homeImg[1]"
                type="image/webp"
              />-->
              <img
                :src="urlBaseCommercial+ 'Commercial-1/'+db.commercials[0].homeImg[0]"
                :alt="db.commercials[0].name"
              />
            </picture>

            <figcaption>{{ db.commercials[0].name }}</figcaption>
          </figure>
        </router-link>

        <router-link
          id="commercialP2"
          :to="{ name: 'DetailsProyect', params: {name: db.commercials[1].url} }"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseCommercial+ 'Commercial-2/'+db.commercials[1].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseCommercial+ 'Commercial-2/'+db.commercials[1].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseCommercial+ 'Commercial-2/'+db.commercials[1].homeImg[0]"
                :alt="db.commercials[1].name"
              />
            </picture>

            <figcaption>{{ db.commercials[1].name }}</figcaption>
          </figure>
        </router-link>
      </section>

      <h2>Otros</h2>
      <section id="othersProyects" class="proyects">
        <router-link
          id="othersP1"
          :to="{ name: 'DetailsProyect', params: {name: db.others[0].url} }"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseOthers+ 'Others-1/'+db.others[0].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseOthers+ 'Others-1/'+db.others[0].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseOthers+ 'Others-1/'+db.others[0].homeImg[0]"
                :alt="db.others[0].name"
              />
            </picture>

            <figcaption>{{ db.others[0].name }}</figcaption>
          </figure>
        </router-link>

        <router-link
          id="othersP2"
          :to="{ name: 'DetailsProyect', params: {name: db.others[1].url} }"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseOthers+ 'Others-2/'+db.others[1].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseOthers+ 'Others-2/'+db.others[1].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseOthers+ 'Others-2/'+db.others[1].homeImg[0]"
                :alt="db.others[1].name"
              />
            </picture>

            <figcaption>{{ db.others[1].name }}</figcaption>
          </figure>
        </router-link>

        <router-link
          id="othersP3"
          :to="{ name: 'DetailsProyect', params: {name: db.others[2].url} }"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseOthers+ 'Others-3/'+db.others[2].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseOthers+ 'Others-3/'+db.others[2].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseOthers+ 'Others-3/'+db.others[2].homeImg[0]"
                :alt="db.others[2].name"
              />
            </picture>

            <figcaption>{{ db.others[2].name }}</figcaption>
          </figure>
        </router-link>

        <router-link
          id="othersP4"
          :to="{ name: 'DetailsProyect', params: {name: db.others[3].url} }"
        >
          <figure>
            <picture>
              <source
                media="(min-width:551px) and (max-width:1080px)"
                :srcset="urlBaseOthers+ 'Others-4/'+db.others[3].homeImg[1]"
                type="image/webp"
              />
              <source
                media="(max-width:550px)"
                :srcset="urlBaseOthers+ 'Others-4/'+db.others[3].homeImg[2]"
                type="image/webp"
              />
              <img
                :src="urlBaseOthers+ 'Others-4/'+db.others[3].homeImg[0]"
                :alt="db.others[3].name"
              />
            </picture>

            <figcaption>{{ db.others[3].name }}</figcaption>
          </figure>
        </router-link>
      </section>
    </div>
  </section>
</template>

<script>
import db from "@/modules/db";

export default {
  name: "Proyects",
  data() {
    return {};
  },
  computed: {
    db() {
      return db;
    },
    urlBaseHousing() {
      return "./img/imgProyects/Housings/";
    },
    urlBaseCommercial() {
      return "./img/imgProyects/Commercials/";
    },
    urlBaseOthers() {
      return "./img/imgProyects/Others/";
    }
  }
};
</script>

<style  lang="scss">
@import "@/styles/variables.scss";

#proyects {
  margin: auto;
  width: 100%;
  overflow: hidden;
  background-color: $color-background;
  min-height: calc(100vh - 134px);
  height: auto;
  color: white;

  #separationProyects {
    height: 99px;
    background-color: $color-background;
  }

  #containerProyects {
    display: grid;
    width: 100%;
    max-width: 2000px;
    margin: auto;
    grid-template-rows: repeat(6, auto);

    h2 {
      font-family: Kodchasan;
      font-weight: bold;
      font-size: 1.5rem;
      line-height: 4rem;
      text-transform: uppercase;
    }

    .proyects {
      display: grid;
      width: 100%;
      margin: auto;
      font-family: Quicksand;
      overflow: hidden;

      a {
        display: block;
        margin: auto;
        width: 100%;
        height: 100%;
        transition: all 300ms;
        color: white;
        text-decoration: none;

        figure {
          width: 100%;
          height: 100%;

          picture img {
            width: 100%;
            height: 100%;
            max-height: 500px;
            object-fit: cover;
            transition: all 300ms;
          }

          figcaption {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            width: 50%;
            max-width: 250px;
            padding: 10px;
            height: 100%;
            background: rgba(0, 0, 0, 0.271);
            font-size: 1.1rem;
            text-transform: uppercase;
            top: -101.3%;
          }

          &:hover picture img {
            opacity: 0.7;
          }
        }
      }
    }

    #housingProyects {
      grid-template-columns: repeat(3, 1fr);
      height: 300px;
      grid-column-gap: 20px;
      grid-row-gap: 20px;

      a {
        height: 300px;
      }
    }

    #commercialProyects {
      grid-template-columns: auto 500px;
      height: auto;
      grid-column-gap: 20px;
      grid-row-gap: 20px;

      a {
        height: 500px;

        figure figcaption {
          top: -100.8%;
        }
      }
    }

    #othersProyects {
      grid-template-columns: 500px auto 500px;
      grid-template-rows: repeat(2, auto);
      grid-column-gap: 20px;
      grid-row-gap: 20px;
      height: auto;

      a {
        height: 500px;

        figure figcaption {
          top: -100.8%;
        }
      }

      #othersP1 {
        grid-column: 1 / 3;
        grid-row: 1 / 2;
      }

      #othersP2 {
        grid-column: 3 / 4;
        grid-row: 1 / 2;
      }

      #othersP3 {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
      }

      #othersP4 {
        grid-column: 2 / 4;
        grid-row: 2 / 3;
      }
    }
  }
}

/* ****************** Responsive **************** */
@media screen and (max-width: 1080px) {
  #proyects {
    #containerProyects {
      #housingProyects {
        grid-template-columns: 100%;
        grid-template-rows: repeat(3, auto);
        height: auto;

        a {
          height: 400px;

          figure figcaption {
            top: -101%;
          }
        }
      }

      #commercialProyects {
        grid-template-columns: 100%;
        grid-template-rows: repeat(2, auto);
        height: auto;

        a {
          height: 400px;

          figure figcaption {
            top: -101%;
          }
        }
      }

      #othersProyects {
        grid-template-columns: 100%;
        grid-template-rows: repeat(4, auto);
        grid-row-gap: 10px;
        height: auto;

        a {
          height: 400px;

          figure figcaption {
            top: -101%;
          }
        }

        #othersP1 {
          grid-column: 1 / 2;
          grid-row: 1 / 2;
        }

        #othersP2 {
          grid-column: 1 / 2;
          grid-row: 2 / 3;
        }

        #othersP3 {
          grid-column: 1 / 2;
          grid-row: 3 / 4;
        }

        #othersP4 {
          grid-column: 1 / 2;
          grid-row: 4 / 5;
        }
      }
    }
  }
}

@media screen and (max-width: 770px) {
  #proyects {
    #containerProyects {
      #housingProyects a,
      #commercialProyects a,
      #othersProyects a {
        figure figcaption {
          top: -100.8%;
        }
      }
    }
  }
}

@media screen and (max-width: 550px) {
  #proyects {
    #containerProyects {
      #housingProyects a,
      #commercialProyects a,
      #othersProyects a {
        height: 300px;

        figure figcaption {
          top: -101%;
        }
      }
    }
  }
}
</style>
