<template>
  <main id="content">
    <PmHeader :windowWidth="windowWidth"></PmHeader>

    <PmHome></PmHome>
    <PmAbout></PmAbout>
    <PmProyects></PmProyects>
    <PmServices></PmServices>
    <PmContact></PmContact>

    <PmFooter></PmFooter>
  </main>
</template>

<script>
import PmHeader from "@/components/layout/Header.vue";
import PmFooter from "@/components/layout/Footer.vue";

import PmHome from "@/components/layout/Home/Home.vue";
import PmAbout from "@/components/layout/Home/About.vue";
import PmProyects from "@/components/layout/Home/Proyects.vue";
import PmServices from "@/components/layout/Home/Services.vue";
import PmContact from "@/components/layout/Home/Contact.vue";

export default {
  name: "Home",
  components: {
    PmHeader,
    PmFooter,
    PmHome,
    PmAbout,
    PmProyects,
    PmServices,
    PmContact
  },
  data() {
    return {
      windowWidth: window.innerWidth
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleWindowResize);
  },
  methods: {
    handleWindowResize() {
      this.windowWidth = window.innerWidth;
    }
  },
  watch: {
    windowWidth: "handleWindowResize"
  }
};
</script>

<style lang="scss">
@import "@/styles/variables.scss";

#content {
  background-color: $color-background;
}
</style>
