<template>
  <main id="detailsProyect">
    <section id="imgProyect">
      <div id="imgSelected">
        <img
          v-show="!show3D"
          :src="urlBaseImg + detailsSelected.img[imgIndex]"
          :alt="detailsSelected.imgTitle[imgIndex]"
        />

        <div v-if="show3D">
          <iframe
            title="A 3D model"
            :width="iframeWidth"
            :height="iframeHeight"
            :src="detailsSelected.url3D"
            frameborder="0"
            allow="autoplay; fullscreen"
            mozallowfullscreen="true"
            webkitallowfullscreen="true"
          ></iframe>
        </div>
      </div>

      <div id="directions" v-show="!show3D">
        <button id="btnPrev" @click.prevent="goToImage(-1)" title="Anterior">&lt;</button>
        <button id="btnFullScreen" @click.prevent="openFullscreen()"></button>
        <button id="btnNext" @click.prevent="goToImage(1)" title="Siguiente">&gt;</button>
      </div>
    </section>

    <section id="proyectsDetails">
      <div :style="'width: calc(120px *' +(detailsSelected.img.length+1)+')'">
        <img
          v-for="(i,index) in detailsSelected.img"
          :key="index"
          :src="urlBaseImg + i"
          :alt="detailsSelected.imgTitle[index]"
          :title="detailsSelected.imgTitle[index]"
          :class="imgActive[index]"
          @click="changeIndex(index)"
        />

        <button :class="active3D" @click="show3DFun()" v-if="detailsSelected.url3D.length >1">3D</button>
      </div>
    </section>

    <section id="details">
      <h1>{{ detailsSelected.name }}</h1>
      <p>{{ detailsSelected.city +", "+ detailsSelected.country }}</p>
      <p>{{ detailsSelected.date }}</p>
    </section>
  </main>
</template>

<script>
import db from "@/modules/db";

export default {
  name: "DetailsProyects",
  data() {
    return {
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
      show3D: false,
      iframeHeight: 0,
      iframeWidth: 0,
      imgIndex: 0,
      imgActive: [],
      active3D: "",
      detailsSelected: {
        name: "",
        url: "",
        city: "",
        country: "",
        date: "",
        imgTitle: [],
        img: [],
        homeImg: [],
        url3D: ""
      },
      urlBaseImg: ""
    };
  },
  mounted() {
    this.getProyect();
    this.sizing3D();
    window.addEventListener("resize", this.handleWindowResize);
  },
  computed: {
    db() {
      return db;
    },
    urlBaseHousing() {
      return "../img/imgProyects/Housings/";
    },
    urlBaseCommercial() {
      return "../img/imgProyects/Commercials/";
    },
    urlBaseOthers() {
      return "../img/imgProyects/Others/";
    }
  },
  methods: {
    getProyect() {
      const proyectName = this.$route.params.name;

      switch (proyectName) {
        case this.db.housings[0].url:
          this.detailsSelected = this.db.housings[0];
          this.urlBaseImg = this.urlBaseHousing + "Housing-1/";
          break;

        case this.db.housings[1].url:
          this.detailsSelected = this.db.housings[1];
          this.urlBaseImg = this.urlBaseHousing + "Housing-2/";
          break;

        case this.db.housings[2].url:
          this.detailsSelected = this.db.housings[2];
          this.urlBaseImg = this.urlBaseHousing + "Housing-3/";
          break;

        case this.db.commercials[0].url:
          this.detailsSelected = this.db.commercials[0];
          this.urlBaseImg = this.urlBaseCommercial + "Commercial-1/";
          break;

        case this.db.commercials[1].url:
          this.detailsSelected = this.db.commercials[1];
          this.urlBaseImg = this.urlBaseCommercial + "Commercial-2/";
          break;

        case this.db.others[0].url:
          this.detailsSelected = this.db.others[0];
          this.urlBaseImg = this.urlBaseOthers + "Others-1/";
          break;

        case this.db.others[1].url:
          this.detailsSelected = this.db.others[1];
          this.urlBaseImg = this.urlBaseOthers + "Others-2/";
          break;

        case this.db.others[2].url:
          this.detailsSelected = this.db.others[2];
          this.urlBaseImg = this.urlBaseOthers + "Others-3/";
          break;

        case this.db.others[3].url:
          this.detailsSelected = this.db.others[3];
          this.urlBaseImg = this.urlBaseOthers + "Others-4/";
          break;
      }

      for (let index = 0; index < this.detailsSelected.img.length; index++) {
        this.imgActive[index] = "";
      }
      this.imgActive[0] = "imgActive";
    },
    changeIndex(index) {
      this.show3D = false;
      this.active3D = "";
      this.imgIndex = index;

      this.imgActive.forEach((element, index) => {
        element = "";
        this.imgActive[index] = element;
      });

      this.imgActive[index] = "imgActive";
    },
    goToImage(n) {
      if (
        (this.imgIndex == 0 && n == -1) ||
        (this.imgIndex == this.detailsSelected.img.length - 1 && n == 1)
      ) {
        this.changeIndex(this.imgIndex);
      } else {
        this.changeIndex((this.imgIndex += n));
      }
    },
    openFullscreen() {
      var imgSelected = document.getElementById("imgProyect");

      if (imgSelected.requestFullscreen) {
        imgSelected.requestFullscreen();
      } else if (imgSelected.mozRequestFullScreen) {
        /* Firefox */
        imgSelected.mozRequestFullScreen();
      } else if (imgSelected.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        imgSelected.webkitRequestFullscreen();
      } else if (imgSelected.msRequestFullscreen) {
        /* IE/Edge */
        imgSelected.msRequestFullscreen();
      }
    },
    handleWindowResize() {
      this.windowWidth = window.innerWidth;
      this.windowHeight = Window.innerHeight;
      if (document.querySelector("#imgSelected") !== null) {
        this.sizing3D();
      }
    },
    sizing3D() {
      const $img3D = document.querySelector("#imgSelected");
      this.iframeHeight = $img3D.clientHeight;
      this.iframeWidth = $img3D.clientWidth;
    },
    show3DFun() {
      this.show3D = true;
      this.active3D = "imgActive";

      this.imgActive.forEach((element, index) => {
        element = "";
        this.imgActive[index] = element;
      });
    }
  },
  watch: {
    windowWidth: "handleWindowResize",
    windowHeight: "handleWindowResize"
  }
};
</script>

