<template>
  <section id="home">
    <div v-show="indexSlide === 1" class="miSlider fade">
      <picture>
        <source
          media="(min-width:551px) and (max-width:1000px)"
          :srcset="urlBaseHome+'home-1-1000.webp'"
          type="image/webp"
        />
        <source media="(max-width:550px)" :srcset="urlBaseHome+'home-1-550.webp'" type="image/webp" />
        <img :src="urlBaseHome+'home-1.webp'" />
      </picture>
    </div>

    <div v-show="indexSlide === 2" class="miSlider fade">
      <picture>
        <source
          media="(min-width:551px) and (max-width:1000px)"
          :srcset="urlBaseHome+'home-2-1000.webp'"
          type="image/webp"
        />
        <source media="(max-width:550px)" :srcset="urlBaseHome+'home-2-550.webp'" type="image/webp" />
        <img :src="urlBaseHome+'home-2.webp'" />
      </picture>
    </div>

    <div v-show="indexSlide === 3" class="miSlider fade">
      <picture>
        <source
          media="(min-width:551px) and (max-width:1000px)"
          :srcset="urlBaseHome+'home-3-1000.webp'"
          type="image/webp"
        />
        <source media="(max-width:550px)" :srcset="urlBaseHome+'home-3-550.webp'" type="image/webp" />
        <img :src="urlBaseHome+'home-3.webp'" />
      </picture>
    </div>

    <div id="directions">
      <button id="btnPrev" @click.prevent="goOnSlider(-1)">&lt;</button>
      <button id="btnNext" @click.prevent="goOnSlider(1)">&gt;</button>
    </div>

    <div v-show="indexSlide === 1" id="texSlide1" class="textSlide">
      <h2>Lorem1 ipsum dolor sit amet consectetur adipisicing elit</h2>
      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Repudiandae harum dolor hic deleniti nihil</p>
    </div>

    <div v-show="indexSlide === 2" id="texSlide2" class="textSlide">
      <h2>Lorem2 ipsum dolor sit amet consectetur adipisicing elit</h2>
      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Repudiandae harum dolor hic deleniti nihil</p>
    </div>

    <div v-show="indexSlide === 3" id="texSlide3" class="textSlide">
      <h2>Lorem3 ipsum dolor sit amet consectetur adipisicing elit</h2>
      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Repudiandae harum dolor hic deleniti nihil</p>
    </div>

    <div id="bars">
      <span class="bar" :class="activeSlide[0]" @click.prevent="positionSlider(1)"></span>
      <span class="bar" :class="activeSlide[1]" @click.prevent="positionSlider(2)"></span>
      <span class="bar" :class="activeSlide[2]" @click.prevent="positionSlider(3)"></span>
    </div>
  </section>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      indexSlide: 1,
      activeSlide: ["active", "", ""]
    };
  },
  mounted() {
    this.timeSlide();
  },
  computed: {
    urlBaseHome() {
      return "./img/imgHome/";
    }
  },
  methods: {
    timeSlide() {
      let x = this;
      setInterval(() => {
        if (x.indexSlide >= 1 && x.indexSlide < 3) {
          x.showSlide(x.indexSlide++);
        } else {
          x.showSlide((x.indexSlide = 1));
        }
      }, 10000);
    },
    goOnSlider(n) {
      this.showSlide((this.indexSlide += n));
    },
    positionSlider(n) {
      this.showSlide((this.indexSlide = n));
    },
    showSlide(n) {
      if (n > 3) {
        this.indexSlide = 1;
      }
      if (n < 1) {
        this.indexSlide = 3;
      }

      if (this.indexSlide === 1) {
        this.activeSlide = ["active", "", ""];
      } else if (this.indexSlide === 2) {
        this.activeSlide = ["", "active", ""];
      } else if (this.indexSlide === 3) {
        this.activeSlide = ["", "", "active"];
      }
    }
  }
};
</script>

<style lang="scss">
#home {
  position: relative;
  padding-top: 100px;
  max-width: 100%;
  width: 100%;
  height: calc(100vh - 35px);
  overflow: hidden;

  .miSlider {
    transition: all 2s;
    height: calc(100vh - 65px);

    picture img {
      width: 100%;
      height: calc(100vh - 135px);
      object-fit: cover;
      vertical-align: bottom;
      transition: all 2s;
    }
  }

  /* ****************** Animación **************** */
  .fade {
    animation-name: fade;
    animation-duration: 2s;
  }
  @keyframes fade {
    from {
      opacity: 0.4;
    }
    to {
      opacity: 1;
    }
  }

  /* ****************** Flechas laterales **************** */
  #directions {
    width: 100%;
    height: calc(100% - 100px);
    position: absolute;
    top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    #btnPrev {
      font-family: Quicksand;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 5%;
      border: none;
      color: rgba(255, 255, 255, 0.692);
      font-size: 1.5rem;
      background: linear-gradient(
          to left,
          #00000000,
          rgba(255, 255, 255, 0.473)
        ),
        transparent;
      cursor: pointer;

      &:hover {
        background: linear-gradient(
            to left,
            #00000000,
            rgba(255, 255, 255, 0.616)
          ),
          transparent;
        color: rgba(255, 255, 255, 0.836);
      }
      &:focus {
        border: none;
        outline: none;
      }
    }

    #btnNext {
      font-family: Quicksand;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 5%;
      border: none;
      color: rgba(255, 255, 255, 0.692);
      font-size: 1.5rem;
      background: linear-gradient(
          to right,
          #00000000,
          rgba(255, 255, 255, 0.473)
        ),
        transparent;
      cursor: pointer;

      &:hover {
        background: linear-gradient(
            to right,
            #00000000,
            rgba(255, 255, 255, 0.616)
          ),
          transparent;
        color: rgba(255, 255, 255, 0.836);
      }
      &:focus {
        border: none;
        outline: none;
      }
    }
  }

  /* ****************** Barra de imagenes **************** */
  #bars {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 15px;

    .bar {
      cursor: pointer;
      height: 3px;
      width: 25px;
      margin: 0 2px;
      background-color: #555555a2;
      display: inline-block;
      margin-left: 3px;
    }

    .active {
      background-color: rgba(255, 255, 255, 0.432);
      animation-name: fade;
      animation-duration: 2s;
    }
  }

  /* ****************** Texto **************** */
  .textSlide {
    width: 250px;
    height: calc(90% - 100px);
    top: calc(100px + 5%);
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;

    h2 {
      font-family: Kodchasan;
      font-size: 1.9rem;
      line-height: 2.3rem;
      border-bottom: 3px solid orange;
    }

    p {
      margin: 0px;
      font-family: Quicksand;
    }
  }

  #texSlide1 {
    left: 7%;

    h2 {
      text-align: left;
    }

    p {
      text-align: left;
    }
  }

  #texSlide2 {
    left: calc(50% - 125px);

    h2 {
      text-align: center;
    }

    p {
      text-align: center;
    }
  }

  #texSlide3 {
    right: 7%;

    h2 {
      text-align: right;
    }

    p {
      text-align: right;
    }
  }
}

/* ****************** Responsive **************** */
@media screen and (max-width: 1199px) {
  #home {
    #directions {
      #btnPrev,
      #btnNext {
        width: 10%;
      }
    }

    #texSlide1 {
      left: 10%;
    }

    #texSlide3 {
      right: 10%;
    }
  }
}

@media screen and (max-width: 770px) {
  #home {
    #directions {
      #btnPrev,
      #btnNext {
        width: 15%;
      }
    }

    #texSlide1 {
      left: 15%;
    }

    #texSlide3 {
      right: 15%;
    }
  }
}

@media screen and (max-width: 550px) {
  #home {
    #directions {
      #btnPrev,
      #btnNext {
        width: 20%;
      }
    }

    .textSlide {
      width: 60%;
    }

    #texSlide1 {
      left: 20%;
    }

    #texSlide2 {
      left: 20%;
    }

    #texSlide3 {
      right: 20%;
    }
  }
}
</style>
