<template>
  <section id="contact">
    <PmAlert :infoAlert="iAlert" @hideAlert="showAlert = $event" v-if="showAlert"></PmAlert>

    <div id="separationContact"></div>

    <div id="containerContact">
      <div id="contactData">
        <h2>Contactanos</h2>

        <form class="contact-form" @submit.prevent="validationForm">
          <label for="user_name">
            nombre
            <span v-if="!$v.nameUser.required">*</span>
            <small v-if="$v.nameUser.$error">&nbsp; Ingresa tu nombre</small>
          </label>
          <input
            id="user_name"
            type="text"
            name="user_name"
            v-model.lazy="$v.nameUser.$model "
            :class="{'invalidInput': $v.nameUser.$error}"
          />

          <label for="user_email">
            email
            <span v-if="!$v.emailUser.required">*</span>
            <small v-if="$v.emailUser.$error">&nbsp; Este correo no es válido.</small>
          </label>
          <input
            id="user_email"
            type="email"
            name="user_email"
            v-model.lazy="$v.emailUser.$model"
            :class="{'invalidInput': $v.emailUser.$error}"
          />

          <label for="user_subjet">
            asunto
            <span v-if="!$v.subjetUser.required">*</span>
            <small v-if="$v.subjetUser.$error">&nbsp; Ingresa el asunto</small>
          </label>
          <input
            id="user_subjet"
            type="text"
            name="user_subjet"
            v-model.lazy="$v.subjetUser.$model"
            :class="{'invalidInput': $v.subjetUser.$error}"
          />

          <label for="user_message">
            mensaje
            <span v-if="!$v.messageUser.required">*</span>
            <small v-if="$v.messageUser.$error">&nbsp; Ingresa el mensaje</small>
          </label>
          <textarea
            id="user_message"
            name="user_message"
            v-model.lazy="$v.messageUser.$model"
            cols="30"
            rows="3"
            :class="{'invalidInput': $v.messageUser.$error}"
          ></textarea>

          <button type="submit">
            Enviar
            <img v-show="sending" src="@/assets/loader.webp" />
          </button>
        </form>
      </div>

      <div id="developed">
        <p>
          &copy; Desarrollado por &nbsp;
          <a :href="refMailDev">Juan Zabatta</a>
        </p>
      </div>

      <aside>
        <h2>Contacto</h2>
        <p>Dirección: Santiago, Chile</p>

        <p id="movil1Contact">
          Móvil
          <a href="tel:+569 49123667 ">+569 49123667</a>
        </p>
        <p id="movil2Contact">
          Móvil
          <a href="tel:+569 45020379 ">+569 45020379</a>
        </p>

        <p>
          <a :href="refMail">{{ mail }}</a>
        </p>
      </aside>
    </div>
  </section>
</template>

<script>
import emailjs from "emailjs-com";
import { required, minLength, email } from "vuelidate/lib/validators";
import PmAlert from "@/components/shared/Alert.vue";

export default {
  name: "Contact",
  components: {
    PmAlert
  },
  data() {
    return {
      nameUser: "",
      emailUser: "",
      subjetUser: "",
      messageUser: "",
      sending: false,
      showAlert: false,
      iAlert: {
        success: true,
        title: "Titulo",
        message: "Mensaje"
      },
      refMail: "",
      mail: "",
      mail1: ["@", "ZGproyectos", "cont", "acto", ".cl"],
      refMailDev: "",
      mailDev1: ["@", "gmail", "juan", "zabatta", ".com"]
    };
  },
  mounted() {
    this.refMail = `mailto: ${this.mail1[2]}${this.mail1[3]}${this.mail1[0]}${this.mail1[1]}${this.mail1[4]}`;
    this.mail = `${this.mail1[2]}${this.mail1[3]}${this.mail1[0]}${this.mail1[1]}${this.mail1[4]}`;

    this.refMailDev = `mailto: ${this.mailDev1[2]}${this.mailDev1[3]}${this.mailDev1[0]}${this.mailDev1[1]}${this.mailDev1[4]}`;
  },
  validations: {
    nameUser: { required, minLength: minLength(2) },
    emailUser: { required, email },
    subjetUser: { required, minLength: minLength(4) },
    messageUser: { required, minLength: minLength(4) }
  },
  methods: {
    validationForm(e) {
      this.$v.$touch();

      if (this.$v.$invalid) {
        console.log("error");
      } else {
        this.sending = true;
        this.sendEmail(e);
      }
    },
    sendEmail(e) {
      emailjs
        .sendForm(
          "contactozabax_gmail_com",
          "Correo_FM8457ASD",
          e.target,
          "user_M996u1G78rqobY8Uj4lK0"
        )
        .then(() => {
          this.sending = false;

          this.iAlert = {
            success: true,
            title: "Exito!!",
            message: "Se envió el correo satisfactoriamente"
          };
          this.showAlert = true;

          this.nameUser = "";
          this.emailUser = "";
          this.subjetUser = "";
          this.messageUser = "";
        })
        .catch(() => {
          this.sending = false;
          this.iAlert = {
            success: false,
            title: "Ups!!",
            message:
              "Parece que ha ocurrido un error al intentar enviar el correo"
          };
          this.showAlert = true;
        });
    }
  }
};
</script>

<style lang="scss">
@import "@/styles/variables.scss";

#contact {
  margin: auto;
  width: 100%;
  height: auto;
  min-height: 100vh;
  overflow: hidden;
  font-family: Quicksand;
  background-color: $color-background;

  #separationContact {
    height: 99px;
    background-color: $color-background;
  }

  #containerContact {
    display: grid;
    grid-template-columns: 70% 30%;
    grid-template-rows: auto 30px;
    height: auto;
    min-height: calc(100vh - 134px);
    background-color: white;
    color: black;

    /* ****************** cuadro para formulario **************** */
    #contactData {
      grid-column: 1/2;
      width: 50%;
      height: auto;
      margin: auto;

      h2 {
        padding: 10px;
        font-size: 1.3rem;
        font-family: Kodchasan;
      }

      form {
        padding: 10px;
        padding-top: 30px;
        display: flex;
        flex-direction: column;
        margin: auto;
        width: 100%;

        label {
          width: 100%;
          padding: 0 10px;
          margin: auto;
          text-align: left;
          text-transform: uppercase;
          font-size: 0.8rem;

          span {
            font-size: 1rem;
            color: red;
          }

          small {
            font-size: 0.6rem;
            text-transform: none;
            color: red;
          }
        }

        input {
          border: none;
          border-bottom: 0.25rem solid orange;
          font-size: 0.8rem;
          line-height: 1.2rem;
          margin: 0 0 30px 0;
          font-family: Quicksand;
          text-align: left;
          padding: 0 10px;

          &:focus {
            outline: none;
          }
        }

        textarea {
          border: none;
          border-bottom: 0.25rem solid orange;
          font-size: 0.8rem;
          line-height: 1.2rem;
          margin: 0 0 30px 0;
          font-family: Quicksand;
          text-align: left;
          padding: 0 10px;
          max-width: 100%;
          max-height: 100px;
          min-width: 50%;
          min-height: 30px;

          &:focus {
            outline: none;
          }
        }

        button {
          margin: auto;
          padding: 10px;
          border-radius: 10px;
          background-color: white;
          border: none;
          letter-spacing: 0.05rem;
          color: #001236;

          &:hover {
            cursor: pointer;
            color: orange;
          }

          &:focus {
            color: orange;
            outline-color: orange;
          }

          img {
            height: 1rem;
            object-fit: scale-down;
          }
        }

        /* ****************** Input Vacío **************** */
        .invalidInput {
          box-shadow: 0 0 3px red;
        }
      }
    }

    /* ****************** Desarrollado por **************** */
    #developed {
      height: 30px;

      p {
        font-size: 0.8rem;
        font-weight: normal;
        padding: 0px;
        color: #001236;

        a {
          font-size: 0.8rem;
          text-decoration: none;
          color: #001236;

          &:hover {
            color: orange;
          }
        }
      }
    }

    /* ****************** Barra lateral **************** */
    aside {
      grid-column: 2/3;
      grid-row: 1/3;
      width: 100%;
      height: 100%;
      background-color: $color-background;
      color: white;
      display: flex;
      flex-direction: column;
      justify-content: center;

      p {
        color: white;
        text-align: center;
        font-size: 1rem;
        font-weight: bold;
        line-height: 1rem;
        margin: 0px;
        padding: 30px 10px;

        a {
          text-decoration: none;
          font-size: 1rem;
          color: white;

          &:hover {
            color: orange;
          }
        }
      }

      #movil1Contact {
        padding-bottom: 10px;
      }

      #movil2Contact {
        padding-top: 0px;
      }
    }
  }
}

/* ****************** Responsive **************** */
@media screen and (max-width: 900px) {
  #contact {
    #containerContact {
      #contactData {
        width: 60%;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  #contact {
    #containerContact {
      #contactData {
        width: 70%;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  #contact {
    #containerContact {
      grid-template-columns: 55% 45%;

      #contactData {
        width: 90%;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  #contact {
    #containerContact {
      #contactData {
        width: 98%;

        h2 {
          font-size: 1.3rem;
        }
      }
    }
  }
}

@media screen and (max-width: 400px) {
  #contact {
    #containerContact {
      #contactData {
        width: 100%;

        h2 {
          font-size: 1.2rem;
        }
      }
    }
  }
}
</style>
